
/* ICON
    SEO
  */


a:link, a:visited, a:active {
  color:#ffffff;;
  text-decoration: none; 
}


a:hover {
  color:#e65624;   
}


.App {
  
  background-color: #0e1b2b;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  text-align: center;
  color: white;
  font-family: 'Source Sans Pro', sans-serif;
  
  
  

}


/*//////////
///   Nav Page
//////////*/
.Nav-wrapper {

}
.social-links {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  margin-top: 5vw;

}

.pc-display {
  display: flex;
  flex-direction: row;
  

}
.logo-pc {  
  width:35vw;
  height:10%;
  object-fit: contain;
  padding-top: 10rem;
  padding-left: 1.5rem;
  
}
.logo-div-pc {
  object-fit: contain;
  height: 100%;

  

}
.nav-links { /* targets div with all navlinks inside */
  text-align: left;
  font-weight: 600;
  object-fit: contain;

}
.NavLink { /*targets indivdual links*/

}
.nav-options { /*targets <NavOptions /> div */
  padding-top: 3vw;
  padding-left: 5vw;
  font-size: 2vw;
  
}
.seperator-pc {
  text-align: left;
  font-weight: 700;
  min-width: 3%;
  

  object-fit: contain;


  height: 100vh;
  
}
.seperator-img-pc {/*targets img element*/
  height: 100%;
  width: 100%;
  padding-right: 0.5rem;
  object-fit: contain;
  
  
}

.outlet-area-pc {
  margin-left:auto;
  margin-right: auto;
  font-size: 1vw;
  
 
}
.outlet-pc { /*targets <Outlet /> */
  
}
.mob-display {
  display: none;
}
.menu-bar-div {
  display: none;
}
/*////////// 
///   Home Page
//////////*/

.home-wrapper {
  height: 100vh;
  width: 100vh;
}
.carousel-item { /*targets coontent inside like title */
  height: 100vh;
  width: 100vh;
  
}
.carousel-container {
  height: 100vh;
  width: 100vh;
  position: absolute;
}

.splash-pic {
  object-fit: contain;
  height:100%;

}
.home-mob {
  display: none;
}


/*//////////
///   About Us
//////////*/
/* Create three equal columns that floats next to each other */
.values-div-pc {
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: flex;
  flex-direction: row;
  border: #fb61d7;
  border-radius: 100px;
  border-style: solid;
}
.values-pc {
  margin: auto;
  border: #73fb61;
  border-radius: 100px;
  border-style: solid;
  
}

.about-column {
  float: left;
  width: 33.33%;
  padding: 2vw;
  
}
.about-row {
  display: table;
  clear: both;
}
.bios-pc {
  display: table;
  clear: both;
  margin:auto;
  border: #fb61d7;
  border-radius: 5px;
  border-style: solid;
}
/*target each image with .staff-pics*/
.staff-pics {
  width: 8rem;
  object-fit: contain;
}
.bio-mob-text {
  display: none;
}
/*//////////
///   Contact
//////////*/

.email-container {
  display: flex;
  flex-direction: column;
}
.contact-row {
  display: flex;
  flex-direction: row;
}
.contact-details {
  text-align: left;
  padding-left: 2rem;
}
.contact-gmap {
  
}
/*//////////
///   Mobile
//////////*/
@media screen and (max-width: 950px) {
  .App {
  
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  color: white;
  font-family: 'Source Sans Pro', sans-serif;
  overflow: scroll;
  
  border: #fbf861;
  border-radius: 5px;
  border-style: solid;
  }

  .Nav-wrapper {
    width:100%;

    object-fit: contain;
  }
  .pc-display {
    display: none;
  }
  .mob-display {
    display: contents;   
    display: flex;
    flex-direction: row;
  

    border: #fb6161;
    border-radius: 5px;
    border-style: solid;

  }
  .menu-bar-div {
    display: flex;
    flex-direction: row;
    height: 2rem;
    width: 100%;
    padding-left: 2rem;
    padding-bottom: 3rem;
    gap: 3rem;
    color: #e65624;
    font-weight: 600;
    font-size: 2rem;

    border: #f161fb;
    border-radius: 5px;
    border-style: solid;
  }
  #menubars {
    padding-top: 0.5rem;
  }
  .mob-nav-col {
    display: flex;
    flex-direction: column;
    width: 90%;
    object-fit: contain;
  }
  
  .logo-mob { /* targets image*/
    height: 100%;
    width: 100%;
    object-fit: contain;
    padding-left: 1rem;
    
    border: #61cdfb;
    border-radius: 5px;
    border-style: solid;
    
  }
  .outlet-area-mob {
    font-size: 2.5vw;
    border: #73fb61;
    border-radius: 5px;
    border-style: solid;
  }

  
  .seperator-mob {    
    display: none;


    border: #fb61f6;
    border-radius: 5px;
    border-style: solid;
  }
  .seperator-img-mob {

    object-fit: contain;
 

  }






  .home-mob {
    display: contents;
    

  }
  .mob-NavOptions {
    margin-top: 7rem;
    margin-left: 20vw;
    font-size: 7vw;
  }
  .social-links {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    margin-top: 10vw;
  }
  .home-pc {
    display: none;
  }
  
  .about-mob {

  }
  .about-column {
    width: 100%;
  }
  .about-row {
    display: flex;
    flex-direction: column;
    clear: both;
  }
  .bios-pc {
    display: flex;
    flex-direction: column;
    clear: both;
    margin:auto;
    border: #fb61d7;
    border-radius: 5px;
    border-style: solid;
  }
  .staff-pics {
    width: 15vw;
    object-fit: contain;
  }
  .bio-text-pc {
    display: none;
  }
  .bio-mob-row {
    display: flex;
    flex-direction: row;

  }
  .bio-mob-text {
    display: contents;
    object-fit: none;
    overflow: visible;
    width:70%;
  }

  @media screen and (max-width: 850px) {
    
    
    

    }
  
    

  

  
}


